body {
    font-family: var(--main-font-family);
    font-size: 14px;
    letter-spacing: 0.04em;
    color: var(--main-color);
    background-color: var(--main-light-color);
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    margin: 0;
  }
  
  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
  
  address {
    font-style: normal;
  }
  
  .list {
    list-style: none;
    padding-left: 0;
    margin: 0;
  }
  
  a {
    text-decoration: none;
    color: inherit;
  }